<template>
  <div>
    <MainView>
      <template #header>
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </template>
      <template #main>
        <div>
          <div class="main-top">
            <el-button style="margin-bottom: 20px" type="primary"
              ></el-button
            >
          </div>
          <el-table
            :data="$store.state.rights.rolesList"
            style="width: 100%"
            border
            class="rolesTable"
          >
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-row
                  :class="[
                    'targetRow',
                    'line',
                    { lineTop: index === 0 ? true : false },
                  ]"
                  v-for="(item1, index) in scope.row.children"
                  :key="item1.id"
                >
                  <el-col class="fristCol">
                    <el-tag
                      closable
                      @close="delRights(scope.row.id, item1.id, 1)"
                      >{{ item1.authName }}</el-tag
                    >
                  </el-col>
                  <el-col>
                    <el-row
                      :class="[
                        'targetRow',
                        {
                          line:
                            index !== item1.children.length - 1 ? true : false,
                        },
                      ]"
                      v-for="(item2, index) in item1.children"
                      :key="item2.id"
                    >
                      <el-col class="secondCol">
                        <el-tag
                          closable
                          type="success"
                          @close="
                            delRights(
                              scope.row.id,
                              item1.children,
                              2,
                              item1.id,
                              item2.id
                            )
                          "
                          >{{ item2.authName }}</el-tag
                        >
                      </el-col>
                      <el-row class="targetRow">
                        <el-col v-for="item3 in item2.children" :key="item3.id">
                          <el-tag
                            closable
                            type="warning"
                            @close="
                              delRights(scope.row.id, item1, 3, item2, item3.id)
                            "
                            >{{ item3.authName }}</el-tag
                          >
                        </el-col>
                      </el-row>
                    </el-row>
                  </el-col>
                </el-row>
              </template>
            </el-table-column>
            <el-table-column type="index" width="50"></el-table-column>
            <el-table-column label="" prop="roleName">
            </el-table-column>
            <el-table-column label="" prop="roleDesc">
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <div class="operate">
                  <el-button
                    @click="modify(scope.row)"
                    size="small"
                    type="primary"
                    icon="el-icon-edit"
                    ></el-button
                  >
                  <el-button
                    @click="del(scope.row.id)"
                    size="small"
                    type="danger"
                    icon="el-icon-delete"
                    ></el-button
                  >
                  <AssignRightsDialog :row="scope.row" />
                </div>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </template>
    </MainView>
  </div>
</template>

<script>
import MainView from "../../components/MainView.vue";
import { mapActions } from "vuex";
import AssignRightsDialog from "../../components/AssignRightsDialog.vue";
export default {
  components: { MainView, AssignRightsDialog },
  data() {
    return {};
  },
  methods: {
    ...mapActions(["getRolesList", "delRole", "delRoleRights"]),
    // 
    del(id) {
      this.delRole(id);
    },
    // 
    // roleId: Id
    // rights
    // level
    // currentRightParent Id
    // currentRightIdId
    delRights(roleId, rights, level, currentRightParent, currentRightId) {
      const fn = this.updateRolesList;
      // 
      if (level === 1) {
        this.delRoleRights({ roleId, rightId: rights, fn });
      }
      // 
      if (level === 2 && rights.length === 1) {
        this.delRoleRights({ roleId, rightId: currentRightParent, fn });
        // 
      } else if (level === 2 && rights.length > 1) {
        this.delRoleRights({ roleId, rightId: currentRightId, fn });
      }
      // 
      if (level === 3) {
        // 
        if (currentRightParent.children.length > 1) {
          this.delRoleRights({ roleId, rightId: currentRightId, fn });
        } else {
          // 
          if (rights.children.length > 1) {
            this.delRoleRights({ roleId, rightId: currentRightParent.id, fn });
            // 
          } else {
            this.delRoleRights({ roleId, rightId: rights.id, fn });
          }
        }
      }
      /* this.delRoleRights({roleId, rightId, fn}) */
    },
    // 
    updateRolesList() {
      this.getRolesList();
    },
  },
  created() {
    this.getRolesList();
  },
};
</script>

<style lang="less" scoped>
.demo-table-expand {
  font-size: 0;
  label {
    width: 90px;
    color: #99a9bf;
  }
  .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
}
</style>