<template>
  <div>
    <MainView>
      <template #header>
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </template>
      <template #main>
        <div>
          <el-table :data="tableData" border style="width: 100%">
            <el-table-column type="index" width="50"></el-table-column>
            <el-table-column prop="authName" label="">
            </el-table-column>
            <el-table-column prop="path" label="">
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-tag v-if="scope.row.level === '0'" type="default"></el-tag>
                <el-tag v-else-if="scope.row.level === '1'" type="success"></el-tag>
                <el-tag v-else type="warning"></el-tag>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </template>
    </MainView>
  </div>
</template>

<script>
import { rightsList } from "../../http/api";
import MainView from "../../components/MainView.vue";
export default {
  components: { MainView },
  data() {
    return {
      tableData: []
    }
  },
  async created() {
    const res = await rightsList('list')
    this.tableData = res.data
  },
};
</script>

