import http from './http'
export const login = (data) => http({
    method: 'post',
    url: '/login',
    data
})
// 
export const getUserList = (params) => http({
    method: 'get',
    url: '/users',
    params
})
// 
export const addUser = (data) => http({
    method: 'post',
    url: '/users',
    data
})
// 
export const changeUserType = ({uid,type}) => http({
    method: 'put',
    url: `/users/${uid}/state/${type}`
})
// 
export const searchUser = (id) => http({
    method: 'get',
    url: `/users/${id}`
})
// 
export const modifyUser = ({id,email,mobile}) => http({
    method: 'put',
    url: `/users/${id}/`,
    data: {email,mobile}
})
// 
export const delUser = (id) => http({
    method: 'delete',
    url: `/users/${id}/`
})
// 
export const assignRole = ({id, rid}) => http({
    method: 'put',
    url: `/users/${id}/role`,
    data: { rid }
})
// 
export const rolesList = () => http({
    method: 'get',
    url: '/roles'
})
// 
export const delRole = (id) => http({
    method: 'delete',
    url: '/roles/' + id
})
// 
export const rightsList = (type) => http({
    method: 'get',
    url: '/rights/' + type
})
// 
export const giveRights = ({id,data}) => http({
    method: 'post',
    url: `/roles/${id}/rights`,
    data: {
        rids: data
    }
})
// 
export const delRoleRights = ({roleId, rightId}) => http({
    method: 'delete',
    url: `roles/${roleId}/rights/${rightId}`
})