<template>
  <div>
    <MainView>
      <template #header>
        <div>
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </div>
      </template>
      <template #main>
        <div>
          <CategoryAdd :tableCatData="tableCatData" />
          <tree-table
            :data="tableCatData"
            show-index
            index-text="#"
            :expand-type="false"
            :selection-type="false"
            :columns="columns"
            border
          >
            <template slot="effect" slot-scope="scope">
              <span
                v-if="!scope.row.cat_deleted"
                class="el-icon-success"
              ></span>
              <span v-else class="el-icon-error"></span>
            </template>
            <template slot="order" slot-scope="scope">
              <el-tag v-if="scope.row.cat_level === 0" type="default"
                ></el-tag
              >
              <el-tag v-else-if="scope.row.cat_level === 1" type="success"
                ></el-tag
              >
              <el-tag v-else type="warning"></el-tag>
            </template>
            <template slot="operate" slot-scope="scope">
              <div class="cat-operate">
                <el-button
                  @click="handleCatEdit(scope.row)"
                  type="primary"
                  size="mini"
                  icon="el-icon-edit"
                  ></el-button
                >
                <el-button
                  @click="handelCatDel(scope.row.cat_id)"
                  type="danger"
                  size="mini"
                  icon="el-icon-delete"
                  ></el-button
                >
              </div>
            </template>
          </tree-table>
          <el-dialog
            title=""
            :visible.sync="dialogEditVisible"
            width="30%"
          >
            <el-form
              :model="categoryForm"
              :rules="categoryRules"
              ref="categoryEditForm"
              class="demo-ruleForm"
              label-width="80px"
            >
              <el-form-item label="" prop="cat_name">
                <el-input v-model="categoryForm.cat_name"></el-input>
              </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
              <el-button @click="dialogEditVisible = false"> </el-button>
              <el-button type="primary" @click="catEdit"> </el-button>
            </span>
          </el-dialog>
          <div class="block">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page.sync="categoryData.pagenum"
              :page-size="categoryData.pagesize"
              :page-sizes="[1, 2, 5, 10]"
              layout="total, sizes, prev, pager, next, jumper"
              :total="catTotal"
            >
            </el-pagination>
          </div>
        </div>
      </template>
    </MainView>
  </div>
</template>

<script>
import {
  goodsCategories,
  goodsEditCategory,
  goodsDelCategory,
} from "../../http/goodsApi";
import MainView from "../../components/MainView.vue";
import CategoryAdd from "../../components/goodsManage/CategoryAdd.vue";
export default {
  components: { MainView, CategoryAdd },
  data() {
    return {
      dialogEditVisible: false,
      categoryForm: {
        cat_name: "",
      },
      categoryRules: {
        cat_name: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      catTotal: 0,
      tableCatData: [],
      columns: [
        {
          label: "",
          prop: "cat_name",
        },
        {
          label: "",
          type: "template",
          template: "effect",
        },
        {
          label: "",
          type: "template",
          template: "order",
        },
        {
          label: "",
          type: "template",
          template: "operate",
        },
      ],
      categoryData: {
        // 
        type: 3,
        // 
        pagenum: 1,
        // 2
        pagesize: 2,
      },
      // id
      editId: 0,
      // 
      originCatName: "",
    };
  },
  methods: {
    // 
    async getCategory() {
      const res = await goodsCategories(this.categoryData);
      if (res.meta.status !== 200) return;
      this.tableCatData = res.data.result;
      this.catTotal = res.data.total;
    },
    // 
    handleSizeChange(val) {
      this.categoryData.pagesize = val;
      this.getCategory();
    },
    // 
    handleCurrentChange(val) {
      this.categoryData.pagenum = val;
      this.getCategory();
    },
    // 
    handleCatEdit(row) {
      this.dialogEditVisible = true;
      const { cat_id, cat_name } = row;
      this.editId = cat_id;
      this.categoryForm.cat_name = cat_name;
      this.originCatName = cat_name;
    },
    // 
    async catEdit() {
      const { cat_name } = this.categoryForm;
      this.$refs.categoryEditForm.validate(async (valid) => {
        if (valid) {
          // 
          if (this.originCatName === cat_name) return;
          const res = await goodsEditCategory(this.editId, { cat_name });
          if (res.meta.status !== 200) return;
          /* const item = this.tableCatData.find((ele) => {
            ele.cat_id === res.data.cat_id;
          });
          console.log(item);
          item.cat_name = res.data.cat_name; */
          let target = this.findId(this.tableCatData, res.data.cat_id);
          target.cat_name = res.data.cat_name;
          this.$message({
            type: "success",
            message: "",
          });
        } else {
          return false;
        }
      });
      this.dialogEditVisible = false;
    },
    // Id
    findId(arr, id) {
      var target = null;
      for (let i = 0; i < arr.length; i++) {
        if (arr[i].cat_id === id) return arr[i];
        if (arr[i].children) target = this.findId(arr[i].children, id);
        if (target !== null) return target;
      }
      return target;
    },
    // 
    async handelCatDel(id) {
      const res = await goodsDelCategory(id);
      if (res.meta.status !== 200) return;
      // 
      this.getCategory();
      this.$message({
        type: "success",
        message: "",
      });
    },
  },
  computed: {
    currentIndex(ind) {
      return this.categoryData.pagesize * (this.categoryData.pagenum - 1) + ind;
    },
  },
  created() {
    this.getCategory();
  },
};
</script>

<style lang="less" scoped>
.el-icon-success {
  color: lightgreen;
  font-size: 18px;
}
.el-icon-error {
  color: red;
  font-size: 18px;
}
</style>