<template>
  <el-table ref="table" :data="currentParams" style="width: 100%" border>
    <el-table-column type="expand">
      <!--  -->
      <template slot-scope="props">
        <div>
          <el-tag
            :key="index"
            v-for="(tag, index) in props.row.attr_vals"
            closable
            :disable-transitions="false"
            @close="handleTagClose(props.row, tag)"
          >
            {{ tag }}
          </el-tag>
          <el-input
            class="input-new-tag"
            v-if="props.row.tagVisible"
            v-model="inputValue"
            ref="saveTagInput"
            size="small"
            @keyup.enter.native="handleInputConfirm(props.row)"
            @blur="handleInputConfirm(props.row)"
          >
          </el-input>
          <el-button
            v-else
            class="button-new-tag"
            size="small"
            @click="showInput(props.row)"
            >+ New Tag</el-button
          >
        </div>
      </template>
    </el-table-column>
    <el-table-column type="index"> </el-table-column>
    <el-table-column label="" prop="attr_name"> </el-table-column>
    <el-table-column label="" width="180">
      <template slot-scope="scope">
        <div>
          <el-button
            size="mini"
            type="primary"
            @click="handleParamsEdit(scope.row)"
            ></el-button
          >
          <el-button
            size="mini"
            type="danger"
            @click="handleParamsDelete(scope.row)"
            ></el-button
          >
          <el-dialog
            :title="`${title}`"
            :visible.sync="dialogVisible"
            width="30%"
          >
            <el-form
              :model="paramsEditForm"
              :rules="paramsRules"
              ref="paramsEditForm"
              label-width="100px"
              class="demo-ruleForm"
            >
              <el-form-item :label="title" prop="attr_name">
                <el-input v-model="paramsEditForm.attr_name"></el-input>
              </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
              <el-button @click="dialogVisible = false"> </el-button>
              <el-button type="primary" @click="paramsEdit"> </el-button>
            </span>
          </el-dialog>
        </div>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import { paramsAdd, paramsDelete, paramsEdit } from "../../http/goodsApi";
export default {
  props: ["currentParams", "title"],
  data() {
    return {
      dialogVisible: false,
      paramsEditForm: {
        attr_name: "",
      },
      // 
      originValue: "",
      // 
      currentRow: null,
      paramsRules: {
        attr_name: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
      },
      inputValue: "",
    };
  },
  methods: {
    // 
    showInput(row) {
      this.$emit("showTagInput", row);
      this.$nextTick(() => {
          this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    // 
    async handleInputConfirm(item) {
      const { attr_id, attr_name, attr_sel } = item;
      let inputValue = this.inputValue;
      if (inputValue) {
        // 
        const res = await paramsAdd(attr_id, {
          attr_name,
          attr_sel,
          attr_vals: inputValue,
        });
        if (res.meta.status === 201) {
          this.$emit("updateParams", item, inputValue);
        }
      }
      this.$emit("hideTagInput", item);
      this.inputValue = "";
    },
    //  tag ()
    async handleTagClose(row, tag) {
      this.$emit("delParamsValue", row, tag);
    },
    // 
    async handleParamsDelete(row) {
      const res = await paramsDelete(row.cat_id, row.attr_id);
      if (res.meta.status === 200) {
        this.$emit("delParams", row.attr_id);
      } else {
        this.$message({
          message: ` ${res.meta.status}`,
          type: "error",
        });
      }
    },
    // 
    handleParamsEdit(row) {
      this.dialogVisible = true;
      this.paramsEditForm.attr_name = row.attr_name;
      this.originValue = row.attr_name;
      this.currentRow = row;
    },
    // 
    async paramsEdit() {
      const { cat_id, attr_id, attr_sel, attr_vals } = this.currentRow;
      const { originValue, paramsEditForm } = this;
      let attrVals = attr_vals.join(",");
      if (originValue === paramsEditForm.attr_name) return;
      const res = await paramsEdit(cat_id, attr_id, {
        attr_name: paramsEditForm.attr_name,
        attr_sel,
        attr_vals: attrVals,
      });
      if (res.meta.status === 200) {
        this.$emit("updateEditParams", res.data);
      }
      this.dialogVisible = false;
    },
  },
  // 
  beforeUpdate() {
    this.$nextTick(() => {
      this.$refs["table"].doLayout();
    });
  },
};
</script>

<style>
.el-tag + .el-tag {
  margin-left: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}
</style>