<template>
  <div>
    <el-button
      @click="modify"
      size="mini"
      type="primary"
      icon="el-icon-edit"
    ></el-button>
    <el-dialog title="" :visible.sync="modifyDialogFormVisible">
      <el-form :model="modifyForm">
        <el-form-item label="" label-width="60px">
          <el-input
            ref="nameInp"
            :disabled="true"
            v-model="modifyForm.username"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="" label-width="60px">
          <el-input
            ref="emailInp"
            v-model="modifyForm.email"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="" label-width="60px">
          <el-input
            ref="mobileInp"
            v-model="modifyForm.mobile"
            autocomplete="off"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="modifyDialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="modifyUser"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions } from 'vuex';
export default {
  props: ["scopeRow"],
  data() {
    return {
      // 
      currentUserData: {},
      // id
      willId: "",
      modifyDialogFormVisible: false,
      // 
      modifyForm: {},
    };
  },
  methods: {
    ...mapActions(['modifyUserItem']),
    // 
    modify() {
      const { username, email, mobile, id } = this.scopeRow;
      this.willId = id;
      this.modifyDialogFormVisible = true;
      // 
      this.modifyForm = { id, username, email, mobile };
      this.currentUserData = {
        username,
        currentEmail: email,
        currentMobile: mobile,
      };
    },
    // 
    modifyUser() {
      const { email, mobile } = this.modifyForm;
      const { currentEmail, currentMobile } = this.currentUserData;
      if (email !== currentEmail || mobile !== currentMobile) {
        this.modifyUserItem({ id: this.willId, email, mobile });
      }
      this.modifyDialogFormVisible = false;
      this.modifyForm = {};
      this.currentUserData = {};
    },
  },
};
</script>

