<template>
  <div>
    <el-button
      @click="openDialog"
      size="mini"
      type="warning"
      icon="el-icon-setting"
    ></el-button>

    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form>
        <el-form-item label="">
          <span>{{ scopeRow.username }}</span>
        </el-form-item>
        <el-form-item label="">
          <span>{{ scopeRow.role_name }}</span>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="value" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.id"
              :value="item.roleName"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="assignRole"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { assignRole, rolesList } from "../../http/api";
export default {
  props: ["scopeRow"],
  data() {
    return {
      dialogFormVisible: false,
      value: "",
      options: [],
    };
  },
  methods: {
    async openDialog() {
      this.dialogFormVisible = true;
      const res = await rolesList();
      this.options = res.data;
    },
    async assignRole() {
      const res = this.options.filter((item) => item.roleName === this.value);
      await assignRole({ id: this.scopeRow.id, rid: res[0].id });
      this.$emit('updateUserList')
      this.dialogFormVisible = false;
    },
  },
};
</script>

