<template>
  <div>
    <MainView>
      <template #header>
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </template>
      <template #main>
        <div>
          <div class="user-operate-top">
            <el-input class="search" type="text" v-model="searchValue">
              <el-button
                @click="searchGoods"
                slot="append"
                icon="el-icon-search"
              ></el-button>
            </el-input>
            <el-button @click="addGoods" type="primary"></el-button>
          </div>
        </div>
        <div class="table-data">
          <el-table :data="tableData.goods" border style="width: 100%">
            <el-table-column
              type="index"
              :index="setIndex"
              width="50"
            ></el-table-column>
            <el-table-column
              prop="goods_name"
              label=""
            ></el-table-column>
            <el-table-column
              prop="goods_price"
              label=""
              width="150px"
            >
            </el-table-column>
            <el-table-column prop="goods_weight" label="" width="150px">
            </el-table-column>
            <el-table-column label="" width="220px">
              <template slot-scope="scope">
                <span>
                  {{ scope.row.add_time | moment("YYYY-MM-DD h:mm:ss") }}
                </span>
              </template>
            </el-table-column>
            <el-table-column label="" width="150px">
              <template slot-scope="scope">
                <div class="goods-edit">
                  <GoodsEdit
                    :currentGoods="scope.row"
                    @updateGoods="getGoods"
                  />
                  <el-button
                    @click="goodsDel(scope.row.goods_id)"
                    type="danger"
                    size="mini"
                    icon="el-icon-delete"
                  ></el-button>
                </div>
              </template>
            </el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page.sync="currentPage"
              :page-sizes="[5, 10, 15, 20]"
              :page-size="pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="tableData.total"
            >
            </el-pagination>
          </div>
        </div>
      </template>
    </MainView>
  </div>
</template>

<script>
import { goodsList, goodsDelete } from "../../http/goodsApi";
import MainView from "../../components/MainView.vue";
import GoodsEdit from "../../components/goodsManage/GoodsEdit.vue";
export default {
  components: { MainView, GoodsEdit },
  data() {
    return {
      searchValue: "",
      tableData: [],
      // 
      currentPage: 1,
      // 
      pageSize: 10,
    };
  },
  methods: {
    // 
    async getGoods() {
      const { searchValue, currentPage, pageSize } = this;
      const res = await goodsList({
        query: searchValue,
        pagenum: currentPage,
        pagesize: pageSize,
      });
      this.tableData = res.data;
    },
    // 
    async handleSizeChange(val) {
      this.pageSize = val;
      this.getGoods();
    },
    // 
    async handleCurrentChange(val) {
      this.currentPage = val;
      this.getGoods();
    },
    // 
    searchGoods() {
      this.getGoods();
      this.searchValue = "";
    },
    // 
    async goodsDel(id) {
      const res = await goodsDelete(id);
      if (res.meta.status === 200) {
        this.tableData.goods = this.tableData.goods.filter(item => item.goods_id !== id)
        this.tableData.total--
      }
    },
    // 
    setIndex(index) {
      return index + 1 + (this.currentPage - 1) * this.pageSize;
    },
    // 
    addGoods() {
      this.$router.push("/goodsAdd");
    },
  },
  async created() {
    const res = await goodsList({
      pagenum: this.currentPage,
      pagesize: this.pageSize,
    });
    this.tableData = res.data;
  },
};
</script>

<style lang="less" scoped>
.user-operate-top {
  display: flex;
  margin-bottom: 20px;
  .el-input {
    width: 300px;
    margin-right: 30px;
  }
}
.el-table {
  font-size: 16px;
  font-weight: bold;
  .goods-edit {
    display: flex;
    justify-content: space-around;
  }
}
</style>