<template>
  <div>
    <MainView>
      <template #header>
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </template>
      <template #main>
        <div>
          <div class="tip"></div>
          <el-steps
            class="step"
            :active="active"
            finish-status="success"
            align-center
          >
            <el-step title=""></el-step>
            <el-step title=""></el-step>
            <el-step title=""></el-step>
            <el-step title=""> </el-step>
            <el-step title=""></el-step>
          </el-steps>
          <el-tabs @tab-click="tabAction" v-model="tabName" tab-position="left">
            <el-tab-pane label="">
              <el-form
                :model="addGoodsData"
                :rules="validateForm"
                ref="addGoodsData"
                label-width="100px"
                class="demo-ruleForm"
              >
                <el-form-item label="" prop="goods_name">
                  <el-input
                    type="text"
                    v-model="addGoodsData.goods_name"
                    autocomplete="on"
                  ></el-input>
                </el-form-item>
                <el-form-item label="" prop="goods_price">
                  <el-input
                    type="number"
                    v-model.number="addGoodsData.goods_price"
                    autocomplete="off"
                  ></el-input>
                </el-form-item>
                <el-form-item label="" prop="goods_weight">
                  <el-input
                    type="number"
                    v-model.number="addGoodsData.goods_weight"
                    autocomplete="off"
                  ></el-input>
                </el-form-item>
                <el-form-item label="" prop="goods_number">
                  <el-input
                    type="number"
                    v-model.number="addGoodsData.goods_number"
                    autocomplete="off"
                  ></el-input>
                </el-form-item>
                <el-form-item prop="goods_cat" label="">
                  <el-cascader
                    v-model="addGoodsData.goods_cat"
                    :options="goodsCategoryList"
                    :props="goodsProps"
                    @change="addCategory"
                  ></el-cascader>
                </el-form-item>
              </el-form>
            </el-tab-pane>
            <el-tab-pane label=""></el-tab-pane>
            <el-tab-pane label=""></el-tab-pane>
            <el-tab-pane label="">
              <el-upload
                class="upload-demo"
                action="http://127.0.0.1:8888/api/private/v1/upload"
                :headers="setHeaders"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :on-success="handleSuccess"
                list-type="picture"
              >
                <el-button size="small" type="primary"></el-button>
              </el-upload>
              <el-dialog
                title=""
                :visible.sync="dialogVisible"
                width="30%"
              >
                <img class="preview-img" :src="imgPreviewUrl" alt="" />
              </el-dialog>
            </el-tab-pane>
            <el-tab-pane label="">
              <quill-editor
                ref="myQuillEditor"
                v-model="content"
                :options="editorOption"
                @blur="onEditorBlur($event)"
                @focus="onEditorFocus($event)"
                @ready="onEditorReady($event)"
              />
              <el-button
                class="add-good"
                type="primary"
                @click="submitForm('addGoodsData')"
                ></el-button
              >
            </el-tab-pane>
          </el-tabs>
        </div>
      </template>
    </MainView>
  </div>
</template>

<script>
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
import { quillEditor } from "vue-quill-editor";
import { goodsCategories, goodsAdd } from "../../http/goodsApi";
import MainView from "../../components/MainView.vue";
export default {
  components: { MainView, quillEditor },
  data() {
    return {
      // 
      content: "",
      editorOption: {
        // Some Quill options...
      },
      active: 0,
      tabName: "",
      // 
      addGoodsData: {
        goods_name: "",
        goods_cat: [],
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0,
        goods_introduce: "",
        pics: [],
        attrs: [],
      },
      // 
      validateForm: {
        goods_name: [{ required: true, message: "" }],
        goods_cat: [{ required: true, message: "" }],
        goods_price: [{ required: true, message: "" }],
        goods_number: [{ required: true, message: "" }],
        goods_weight: [{ required: true, message: "" }],
      },
      goodsCategoryList: [],
      // 
      goodsProps: {
        expandTrigger: "click",
        label: "cat_name",
        value: "cat_id",
      },
      // 
      setHeaders: {
        Authorization: sessionStorage.getItem("token"),
      },
      dialogVisible: false,
      // 
      imgPreviewUrl: "",
    };
  },
  methods: {
    next() {
      if (this.active++ > 2) this.active = 0;
    },
    tabAction() {
      this.active = Number(this.tabName);
    },
    addCategory(value) {
      // 
      this.addGoodsData.goods_cat = value.join();
    },
    // 
    handlePreview(file) {
      this.imgPreviewUrl = file.response.data.url;
      this.dialogVisible = true;
    },
    // 
    handleRemove(file) {
      this.addGoodsData.pics = this.addGoodsData.pics.filter((item) => {
        return item.pic !== file.response.data.tmp_path;
      });
    },
    // 
    handleSuccess(response) {
      if (response.data) {
        let imgObj = { pic: response.data.tmp_path };
        this.addGoodsData.pics.push(imgObj);
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const res = await goodsAdd(this.addGoodsData)
          console.log(res);
        } else {
          this.$message({
            message: '',
            type: "error",
          });
          return false;
        }
      });
    },
    // 
    onEditorBlur(quill) {
      console.log("editor blur!", quill);
    },
    onEditorFocus(quill) {
      console.log("editor focus!", quill);
    },
    onEditorReady(quill) {
      console.log("editor ready!", quill);
    },
    onEditorChange({ quill, html, text }) {
      console.log("editor change!", quill, html, text);
      this.content = html;
    },
  },
  async created() {
    const res = await goodsCategories();
    this.goodsCategoryList = res.data;
  },
};
</script>

<style lang="less" scoped>
.tip {
  background-color: #eee;
  line-height: 36px;
  margin-bottom: 20px;
  text-align: center;
  color: gray;
}
.step {
  margin-bottom: 20px;
}
.preview-img {
  width: 100%;
}
/deep/ .ql-editor {
  height: 300px;
}
.add-good {
  margin-top: 20px;
}
</style>