<template>
  <div>
    <el-button
      @click="openCatDialog"
      class="category-add"
      type="primary"
      size="small"
      ></el-button
    >
    <el-dialog
      @close="cancelAddCat"
      title=""
      :visible.sync="dialogAddCatVisible"
      width="30%"
    >
      <el-form
        :model="categoryForm"
        :rules="categoryRules"
        ref="categoryForm"
        class="demo-ruleForm"
        label-width="80px"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="categoryForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="" width="80">
          <el-cascader
            ref="cascader"
            :options="goodsCategoryList"
            :props="categoryProps"
            @change="changeCategory"
            clearable
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelAddCat"> </el-button>
        <el-button type="primary" @click="addCategory"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { goodsCategories, goodsAddCategory } from "../../http/goodsApi";
export default {
  props: ["tableCatData"],
  data() {
    return {
      dialogAddCatVisible: false,
      categoryForm: {
        cat_name: "",
      },
      categoryRules: {
        cat_name: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      goodsCategoryList: [],
      categoryProps: {
        expandTrigger: "click",
        label: "cat_name",
        value: "cat_id",
        checkStrictly: true,
      },
      // 
      categoryAddData: {
        cat_pid: 0,
        cat_name: "",
        cat_level: 0,
      },
    };
  },
  methods: {
    // 
    openCatDialog() {
      this.dialogAddCatVisible = true;
      this.categoryAddData.cat_name = this.categoryForm.cat_name;
    },
    // 
    clearAddData() {
      this.categoryAddData = {
        cat_pid: 0,
        cat_name: "",
        cat_level: 0,
      };
      this.categoryForm.cat_name = "";
      this.$refs.cascader.$refs.panel.clearCheckedNodes();
    },
    // 
    addCategory() {
      this.$refs.categoryForm.validate(async (valid) => {
        this.categoryAddData.cat_name = this.categoryForm.cat_name;
        if (valid) {
          const res = await goodsAddCategory(this.categoryAddData);
          if (res.meta.status !== 201) return;
          console.log(res);
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
      this.clearAddData();
    },
    // 
    cancelAddCat() {
      this.clearAddData();
      this.dialogAddCatVisible = false;
    },
    // 
    changeCategory(value) {
      const parentId = value[value.length - 1];
      this.categoryAddData.cat_pid = parentId;
      this.categoryAddData.cat_level = value.length;
    },
  },
  async created() {
    const res = await goodsCategories({ type: 2 });
    this.goodsCategoryList = res.data;
  },
};
</script>

<style lang="less" scoped>
.category-add {
  margin-bottom: 20px;
}
</style>