<template>
  <div>
    <MainView>
      <template #header>
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </template>
      <template #main>
        <div>
          <el-alert
            class="params-alert"
            title=""
            type="warning"
            show-icon
          >
          </el-alert>
          <div class="category-select">
            <span></span>
            <el-cascader
              v-model="goods_cat"
              :options="goodsCategoryList"
              :props="goodsProps"
              @change="addCategory"
            ></el-cascader>
          </div>
          <el-tabs v-model="activeName" @tab-click="toggleTab">
            <el-tab-pane label="" name="many">
              <GoodsParamsAdd
                title=""
                :catId="thirdId"
                @addNewParams="addNewParams"
              />
              <ParamsShow
                :currentParams="currentParams"
                @updateParams="updateParams"
                @delParamsValue="delParamsValue"
                @delParams="delParams"
                @updateEditParams="updateEditParams"
                @showTagInput="showTagInput"
                @hideTagInput="hideTagInput"
                title=""
              />
            </el-tab-pane>
            <el-tab-pane label="" name="only">
              <GoodsParamsAdd
                title=""
                :catId="thirdId"
                @addNewParams="addNewParams"
              />
              <ParamsShow
                :currentParams="currentParams"
                @updateParams="updateParams"
                @delParamsValue="delParamsValue"
                @delParams="delParams"
                @updateEditParams="updateEditParams"
                @showTagInput="showTagInput"
                @hideTagInput="hideTagInput"
                title=""
              />
            </el-tab-pane>
          </el-tabs>
        </div>
      </template>
    </MainView>
  </div>
</template>

<script>
import { goodsCategories, paramsList } from "../../http/goodsApi";
import MainView from "../../components/MainView.vue";
import ParamsShow from "../../components/goodsManage/ParamsShow.vue";
import GoodsParamsAdd from "../../components/goodsManage/GoodsParamsAdd.vue";
export default {
  components: { MainView, ParamsShow, GoodsParamsAdd },
  data() {
    return {
      goods_cat: [], //  value 
      goodsCategoryList: [],
      // 
      goodsProps: {
        expandTrigger: "click",
        label: "cat_name",
        value: "cat_id",
      },
      activeName: "many",
      manyParams: [], // 
      onlyParams: [], // 
      thirdId: 0, // 
    };
  },
  methods: {
    // 
    async addCategory() {
      // 4
      this.thirdId = this.goods_cat[2];
      if (this.thirdId) {
        this.getParams();
      } else {
        this.manyParams = [];
        this.onlyParams = [];
        this.$message({
          message: "",
          type: "error",
        });
      }
    },
    async toggleTab() {
      //  tab
      if (!this.thirdId) return;
      this.getParams();
    },
    // 
    async getParams() {
      const res = await paramsList(this.thirdId, this.activeName);
      //  tag
      res.data.forEach((ele) => {
        // 
        if (ele.attr_vals) {
          ele.attr_vals = ele.attr_vals.split(",");
        } else {
          ele.attr_vals = [];
        }
        // 
        ele.tagVisible = false;
      });
      if (this.activeName === "many") {
        this.manyParams = res.data;
      } else {
        this.onlyParams = res.data;
      }
    },
    // 
    updateParams(row, value) {
      row.attr_vals.push(value);
    },
    // 
    addNewParams(param) {
      if (param.attr_vals) {
        param.attr_vals = param.attr_vals.split(",");
      } else {
        param.attr_vals = [];
      }
      param.tagVisible = false;
      if (param.attr_sel === "many") {
        this.manyParams.unshift(param);
      } else {
        this.onlyParams.unshift(param);
      }
    },
    // 
    delParams(targetId) {
      if (this.activeName === "many") {
        this.manyParams = this.manyParams.filter(
          (ele) => ele.attr_id !== targetId
        );
      } else {
        this.onlyParams = this.onlyParams.filter(
          (ele) => ele.attr_id !== targetId
        );
      }
      this.$message({
        message: "",
        type: "success",
      });
    },
    // 
    delParamsValue(row, tag) {
      row.attr_vals.splice(row.attr_vals.indexOf(tag), 1);
      this.$message({
        message: "",
        type: "success",
      });
    },
    // 
    updateEditParams(data) {
      let editItem;
      if (data.attr_sel === "many") {
        editItem = this.manyParams.find((ele) => ele.attr_id === data.attr_id);
        editItem.attr_name = data.attr_name;
      } else {
        editItem = this.onlyParams.find((ele) => ele.attr_id === data.attr_id);
        editItem.attr_name = data.attr_name;
      }
      this.$message({
        message: "",
        type: "success",
      });
    },
    // 
    showTagInput(row) {
      row.tagVisible = true;
    },
    // 
    hideTagInput(row) {
      row.tagVisible = false;
    },
  },
  computed: {
    // 
    currentParams() {
      return this.activeName === "many" ? this.manyParams : this.onlyParams;
    },
  },
  async created() {
    const res = await goodsCategories();
    this.goodsCategoryList = res.data;
  },
};
</script>

<style lang="less" scoped>
/deep/ .el-table__expanded-cell[class*="cell"] {
  padding: 20px 60px !important;
}
.params-alert {
  margin-bottom: 20px;
}
</style>