<template>
  <div class="attr-add">
    <el-button :disabled="catId === 0 || catId === undefined" @click="handleAddClick" type="primary" size="small">{{ title }}</el-button>
    <el-dialog
      :title="`${title}`"
      :visible.sync="dialogAddVisible"
      width="30%"
    >
      <el-form
        :model="paramsAddForm"
        :rules="paramsAddRules"
        ref="paramsAddForm"
        class="demo-ruleForm"
      >
        <el-form-item :label="`${title}`" prop="attr_name">
          <el-input v-model="paramsAddForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogAddVisible = false"> </el-button>
        <el-button type="primary" @click="paramsAdd"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { paramsAdd } from "../../http/goodsApi";
export default {
  props: ["title", 'catId'],
  data() {
    return {
      dialogAddVisible: false,
      paramsAddForm: {
        attr_name: ""
      },
      paramsAddRules: {
        attr_name: [
          {required: true, message: '', trigger: 'blur'}
        ]
      }
    };
  },
  methods: {
    handleAddClick() {
      this.dialogAddVisible = true
    },
    async paramsAdd() {
      const attr_sel = this.title === '' ? 'many' : 'only'
      const res = await paramsAdd(this.catId, {attr_name: this.paramsAddForm.attr_name, attr_sel})
      if(res.meta.status === 201) {
        this.$emit('addNewParams', res.data)
      }
      this.dialogAddVisible = false
    }
  },
};
</script>

<style lang="less" scoped>
.attr-add {
  margin-bottom: 20px;
}
</style>