<template>
  <div>
    <MainView>
      <template #header>
        <div>
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </div>
      </template>
    </MainView>
    <el-card class="chart-card"><div id="main"></div></el-card>
  </div>
</template>

<script>
//  echarts  echarts 
import * as echarts from "echarts/core";
//  Chart
import { LineChart } from "echarts/charts";
//  Component
import {
  TitleComponent,
  TooltipComponent,
  GridComponent,
  DatasetComponent,
  TransformComponent,
  LegendComponent,
} from "echarts/components";
// 
import { LabelLayout, UniversalTransition } from "echarts/features";
//  Canvas  CanvasRenderer  SVGRenderer 
import { CanvasRenderer } from "echarts/renderers";

// 
echarts.use([
  TitleComponent,
  TooltipComponent,
  GridComponent,
  DatasetComponent,
  TransformComponent,
  LineChart,
  LabelLayout,
  UniversalTransition,
  CanvasRenderer,
  LegendComponent,
]);
import { reportsData } from "../../http/report";
import MainView from "../../components/MainView.vue";
export default {
  components: { MainView },
  data() {
    return {
      option: {
        title: {
          text: "",
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#E9EEF3",
            },
          },
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true,
        },
        xAxis: [
          {
            boundaryGap: false,
          },
        ],
        yAxis: [
          {
            type: "value",
          },
        ],
      },
    };
  },
  async mounted() {
    const res = await reportsData();
    var myChart = echarts.init(document.getElementById("main"));
    let options = { ...this.option, ...res.data };
    myChart.setOption(options);
  },
};
</script>

<style scoped>
#main {
  width: 800px;
  height: 600px;
}
.chart-card {
    margin-top: 20px;
}
</style>