<template>
  <div>
    <el-button
      icon="el-icon-position"
      size="small"
      type="warning"
      @click="handleprogress"
    ></el-button>
    <el-dialog
      title=""
      :visible.sync="dialogprogressVisible"
      width="60%"
    >
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in progress"
          :key="index"
          :timestamp="activity.time"
        >
          {{ activity.context }}
        </el-timeline-item>
      </el-timeline>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogprogressVisible = false"> </el-button>
        <el-button type="primary" @click="progressEdit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import progress from "../../plugins/address/progress";
export default {
  data() {
    return {
      dialogprogressVisible: false,
      progress
    };
  },
  methods: {
    handleprogress() {
      this.dialogprogressVisible = true;
    },
    progressEdit() {
      this.dialogprogressVisible = false
    }
  },
};
</script>

