<template>
  <div class="login">
    <el-form
      :model="ruleForm"
      status-icon
      :rules="rules"
      ref="ruleForm"
      label-width="50px"
      class="demo-ruleForm"
    >
      <el-form-item label="" prop="account">
        <el-input
          type="text"
          v-model="ruleForm.account"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="pass">
        <el-input
          type="password"
          v-model="ruleForm.pass"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"
          ></el-button
        >
        <el-button @click="resetForm('ruleForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { login } from "../http/api";
export default {
  data() {
    return {
      ruleForm: {
        account: "admin",
        pass: "123456",
      },
      rules: {
        account: [
          {
            validator(rule, value, callback) {
              if (value === "") {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur",
          },
        ],
        pass: [
          {
            validator(rule, value, callback) {
              if (value === "") {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        const { account, pass } = this.ruleForm
        if (valid) {
          const res = await login({username: account, password: pass})
          sessionStorage.setItem('token', res.data.token)
          this.$router.push('/')
        } else {
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
};
</script>

<style lang="less" scoped>
.login {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: skyblue;
  .el-form {
    min-width: 25%;
    padding: 20px;
    box-shadow: 0 0 2px 2px #ddd;
    background-color: #fff;
  }
}
</style>