import Vue from "vue";
import VueRouter from "vue-router";
import LoginView from '../views/LoginView.vue'
import HomeView from '../views/HomeView.vue'
import WelcomeView from '../views/WelcomeView.vue'
import UserList from '../views/UserList.vue'
import RolesList from '../views/rightsManage/RolesList.vue'
import RightsList from '../views/rightsManage/RightsList.vue'
import GoodsList from '../views/goodsManage/GoodsList.vue'
import GoodsAdd from '../views/goodsManage/GoodsAdd.vue'
import GoodsParams from '../views/goodsManage/GoodsParams.vue'
import GoodsCategories from '../views/goodsManage/GoodsCategories.vue'
import OrdersList from '../views/ordersManage/OrdersList.vue'
import ReportView from '../views/reports/ReportView.vue'
Vue.use(VueRouter)
const routes = [
    {
        path: '/login',
        name: 'login',
        component: LoginView
    },
    {
        path: '/',
        component: HomeView,
        children: [
            {
                path: '',
                name: 'home',
                component: WelcomeView
            },
            {
                path: '/userList',
                component: UserList
            },
            {
                path: '/rolesList',
                component: RolesList
            },
            {
                path: '/rightsList',
                component: RightsList
            },
            {
                path: '/goodsList',
                component: GoodsList
            },
            {
                path: '/goodsAdd',
                component: GoodsAdd
            },
            {
                path: '/goodsParams',
                component: GoodsParams
            },
            {
                path: '/goodsCategories',
                component: GoodsCategories
            },
            {
                path: '/ordersList',
                component: OrdersList
            },
            {
                path: '/reportView',
                component: ReportView
            },
        ]
    },
    {
        path: '*',
        component: () => import("../views/NotFound.vue")
    }
]

const router = new VueRouter({
    mode: 'hash',
    routes
})
router.beforeEach((to, from, next) => {
    if (to.path !== '/login' && !sessionStorage.getItem('token')) {
        next('/login')
    } else {
        next()
    }
})
export default router