import { rolesList, delRole, rightsList, giveRights, delRoleRights } from '../http/api'
const rights = {
    state: {
        rolesList: [],
        rightsList: []
    },
    mutations: {
        getRolesList(state, data) {
            state.rolesList = data
        },
        delRole(state, id) {
            state.rolesList = state.rolesList.filter(ele => ele.id !== id)
        },
        getRightsList(state, data) {
            state.rightsList = data
        },
        giveRights(state, callback) {
            callback()
        },
        delRoleRights(state, {data,fn}) {
            state.rolesList.children = data
            fn()
        }
    },
    actions: {
        async getRolesList({ commit }) {
            const res = await rolesList()
            commit('getRolesList', res.data)
        },
        async delRole({ commit }, id) {
            await delRole(id)
            commit('delRole', id)
        },
        async getRightsList({ commit }, type) {
            const res = await rightsList(type)
            commit('getRightsList', res.data)
        },
        async giveRights({ commit }, data) {
            const res = await giveRights(data)
            if (res.meta.status === 200) {
                commit('giveRights', data.callback)
            } else {
                alert(`${res.meta.msg}${res.meta.status}`)
            }
        },
        async delRoleRights({ commit }, {roleId, rightId, fn}) {
            const res = await delRoleRights({roleId, rightId})
            commit('delRoleRights', {data: res.data,fn})
            
        },
    }
}
export default rights