<template>
  <div>
    <MainView>
      <template #header>
        <div>
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </div>
      </template>
      <template #main>
        <div>
            <!-- query -->
          <el-input class="search" type="text" v-model="ordersValue">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
          <el-table :data="tableOrdersData" border style="width: 100%">
            <el-table-column type="index"> </el-table-column>
            <el-table-column prop="order_number" label="">
            </el-table-column>
            <el-table-column prop="order_price" label="">
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <div>
                  <el-tag type="success" v-if="scope.row.pay_status === '1'"
                    ></el-tag
                  >
                  <el-tag type="warning" v-else></el-tag>
                </div>
              </template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <div>
                  <span v-if="scope.row.is_send === ''"></span>
                  <span v-else></span>
                </div>
              </template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <div>
                  {{ scope.row.create_time | moment("YYYY-MM-DD h:mm:ss") }}
                </div>
              </template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <div class="operate">
                  <OrdersEdit :orderItem="scope.row" />
                  <ProgressView />
                </div>
              </template>
            </el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page.sync="ordersData.pagenum"
              :page-size="ordersData.pagesize"
              :page-sizes="[1, 2, 5, 10]"
              layout="total, sizes, prev, pager, next, jumper"
              :total="ordersTotal"
            >
            </el-pagination>
          </div>
        </div>
      </template>
    </MainView>
  </div>
</template>

<script>
import { ordersList } from "../../http/ordersApi";
import MainView from "../../components/MainView.vue";
import OrdersEdit from "../../components/ordersManage/OrdersEdit.vue";
import ProgressView from "../../components/ordersManage/ProgressView.vue";
export default {
  components: { MainView, OrdersEdit, ProgressView },
  data() {
    return {
      ordersValue: "",
      searchOriginValue: "",
      tableOrdersData: [],
      ordersTotal: 0,
      ordersData: {
        query: "",
        // 
        pagenum: 1,
        // 2
        pagesize: 2,
      },
    };
  },
  methods: {
    // 
    async getOrders() {
      const res = await ordersList(this.ordersData);
      if (res.meta.status !== 200) return;
      this.tableOrdersData = res.data.goods;
      this.ordersTotal = res.data.total;
      // 
      /* if(query) {
        this.searchOriginValue = this.searchOrders;
      } */
    },
    // 
    handleSizeChange(val) {
      this.ordersData.pagesize = val;
      this.getOrders();
    },
    // 
    handleCurrentChange(val) {
      this.ordersData.pagenum = val;
      this.getOrders();
    },
  },
  created() {
    this.getOrders();
  },
};
</script>

<style lang="less" scoped>
.search {
  max-width: 350px;
  margin-bottom: 20px;
}
.operate {
    display: flex;
    justify-content: space-around;
}
</style>