import http from './http'
// 
export const goodsList = (data) => http({
    method: 'get',
    url: '/goods',
    params: data
})
// 
export const goodsFind = (id) => http({
    method: 'get',
    url: '/goods/' + id
})
// 
export const goodsDelete = (id) => http({
    method: 'delete',
    url: '/goods/' + id
})
// 
export const goodsAdd = (data) => http({
    method: 'post',
    url: 'goods',
    data
})
// 
export const goodsEdit = (id, data) => {
    console.log(id);
    console.log(data);
    return http({
        method: 'put',
        url: 'goods/' + id,
        data
    })
}

// 
export const goodsCategories = (params) => http({
    method: 'get',
    url: 'categories',
    params
})
// 
export const goodsAddCategory = (data) => http({
    method: 'post',
    url: 'categories',
    data
})
// 
export const goodsEditCategory = (id,data) => http({
    method: 'put',
    url: 'categories/' + id,
    data
})
// 
export const goodsDelCategory = (id) => http({
    method: 'delete',
    url: 'categories/' + id
})

// 
export const paramsList = (id, sel) => http({
    method: 'get',
    url: `categories/${id}/attributes`,
    params: { sel }
})
// 
export const paramsAdd = (id, data) => http({
    method: 'post',
    url: `categories/${id}/attributes`,
    data
})
// 
export const paramsDelete = (id, attrid) => http({
    method: 'delete',
    url: `categories/${id}/attributes/${attrid}`
})
// 
export const paramsEdit = (id, attrid, data) => http({
    method: 'put',
    url: `categories/${id}/attributes/${attrid}`,
    data
})

