import { getUserList, addUser, changeUserType, searchUser, modifyUser, delUser } from "../http/api";
const users = {
    state: {
        tableData: [],
    },
    mutations: {
        addUserItem(state, user) {
            state.tableData.push(user)
        },
        modifyUserItem(state, user) {
            const { mobile, email, id } = user
            const item = state.tableData.find(ele => ele.id === id)
            console.log(item, { mobile, email });
            Object.assign(item, { mobile, email })
        },
        delUserItem(state, id) {
            state.tableData = state.tableData.filter(ele => ele.id !== id)
        },
        sizeChange(state, data) {
            state.tableData = data
        },
        changeState(state, data) {
            const { mg_state, id } = data
            const user = state.tableData.find((ele) => ele.id === id);
            user.mg_state = mg_state ? true : false
        },
        searchUser(state, id) {
            state.tableData = state.tableData.filter((ele) => ele.id === id);
        },
        getUserList(state, user) {
            state.tableData = user
        }
    },
    actions: {
        async addUserItem({ commit }, data) {
            const res = await addUser(data)
            commit('addUserItem', res.data)
        },
        async modifyUserItem({ commit }, data) {
            const res = await modifyUser(data)
            commit('modifyUserItem', res.data)
        },
        async delUserItem({ commit }, id) {
            await delUser(id)
            commit('delUserItem', id)
        },
        // 
        async sizeChange({ commit }, data) {
            const res = await getUserList(data);
            commit('sizeChange', res.data.users)
        },
        async currentPageChange({ commit }, data) {
            const res = await getUserList(data)
            commit('sizeChange', res.data.users)
        },
        async changeState({ commit }, data) {
            const res = await changeUserType(data)
            commit('changeState', res.data)
        },
        async searchUser({ commit }, id) {
            await searchUser(id)
            commit('searchUser',id)
        },
        async getUserList({ commit }, data) {
            const res = await getUserList(data)
            commit('getUserList', res.data.users)
        }
    }
}
export default users