<template>
  <div>
    <el-button
      @click="openEditDialog"
      type="primary"
      size="mini"
      icon="el-icon-edit"
    ></el-button>
    <el-dialog title="" :visible.sync="dialogVisible" width="30%">
      <el-form
        v-if="ruleForm"
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="80px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="goods_name">
          <el-input v-model="ruleForm.goods_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_price">
          <el-input v-model.number="ruleForm.goods_price"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_number">
          <el-input v-model.number="ruleForm.goods_number"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_weight">
          <el-input v-model.number="ruleForm.goods_weight"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="ruleForm.goods_introduce"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="submitEdit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { goodsEdit } from "../../http/goodsApi";
export default {
  props: ["currentGoods"],
  data() {
    return {
      dialogVisible: false,
      ruleForm: null,
      rules: {
        goods_name: [
          { required: true, message: "", trigger: "blur" },
        ],
        goods_price: [
          { required: true, message: "", trigger: "blur" },
        ],
        goods_number: [
          { required: true, message: "", trigger: "blur" },
        ],
        goods_weight: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      goodsId: "",
    };
  },
  methods: {
    // 
    async submitEdit() {
      this.dialogVisible = false;
      // msg: "" status: 400
      await goodsEdit(this.goodsId, this.ruleForm);
      this.$emit("getGoods");
    },
    openEditDialog() {
      this.dialogVisible = true;
      const { goods_id, goods_name, goods_price, goods_number, goods_weight } =
        this.currentGoods;
      this.ruleForm = {
        goods_name,
        goods_price,
        goods_number,
        goods_weight,
        goods_introduce: "",
      };
      this.goodsId = goods_id;
    },
  },
};
</script>

