<template>
  <div class="add-user">
    <el-button @click="dialogFormVisible = true" class="add" type="primary"
      ></el-button
    >
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="form">
        <el-form-item label="" label-width="60px">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="60px">
          <el-input v-model="form.password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="60px">
          <el-input v-model="form.email" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="60px">
          <el-input v-model="form.mobile" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  data() {
    return {
      dialogFormVisible: false,
      // 
      form: {
        name: "",
        password: "",
        email: "",
        mobile: "",
      },
    };
  },
  methods: {
    ...mapActions(["addUserItem"]),
    // 
    addUser() {
      const { name, password, email, mobile } = this.form;
      this.addUserItem({ username: name, password, email, mobile });
      this.dialogFormVisible = false;
    },
  },
};
</script>