<template>
  <div>
    <el-button
      @click="assign()"
      size="small"
      type="warning"
      icon="el-icon-setting"
      class="assign"
      ></el-button
    >
    <el-dialog title="" :visible.sync="dialogVisible" width="30%">
      <el-tree
        :data="$store.state.rights.rightsList"
        show-checkbox
        default-expand-all
        node-key="id"
        ref="tree"
        highlight-current
        :props="defaultProps"
        :default-checked-keys="defaultCheckedKeys"
      >
      </el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="assignRights()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  props: ["row"],
  data() {
    return {
      dialogVisible: false,
      defaultCheckedKeys: [],
      defaultProps: {
        children: "children",
        label: "authName",
      },
      // id
      currentRoleId: 0,
    };
  },
  methods: {
    ...mapActions(["getRightsList", "giveRights", "getRolesList"]),
    // 
    assign() {
      this.dialogVisible = true;
      this.currentRoleId = this.row.id;
      this.getRightsList("tree");
      this.addRights(this.row,this.row.id)
    },
    // 
    addRights(row,id) {
      if (!row.children) {
        return this.defaultCheckedKeys.push(id)
      }
      row.children.forEach(child => {
        this.addRights(child, child.id)
      })
    },
    // 
    assignRights() {
      const childrenKeys = this.$refs.tree.getCheckedKeys();
      const halfNodesKey = this.$refs.tree.getHalfCheckedKeys();
      const data = halfNodesKey.concat(childrenKeys).join();
      const callback = this.updateRolesList;
      this.giveRights({ id: this.currentRoleId, data, callback });
      this.dialogVisible = false
    },
    // 
    updateRolesList() {
      this.getRolesList();
    },
  },
};
</script>

<style>
.assign {
    margin-left: 10px;
}
</style>