<template>
  <div class="user-list">
    <MainView>
      <template #header>
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </template>
      <template #main>
        <div class="user-main">
          <div class="user-operate-top">
            <el-input class="search" type="text" v-model="searchValue">
              <el-button
                @click="search"
                slot="append"
                icon="el-icon-search"
              ></el-button>
            </el-input>
            <AddUserDialog />
          </div>
          <div class="user-show">
            <el-table
              border
              ref="singleTable"
              :data="showUserData"
              highlight-current-row
              style="width: 100%"
            >
              <el-table-column type="index" width="50"></el-table-column>
              <el-table-column prop="username" label=""> </el-table-column>
              <el-table-column prop="email" label=""> </el-table-column>
              <el-table-column prop="mobile" label=""> </el-table-column>
              <el-table-column prop="role_name" label=""> </el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-switch
                    :value="scope.row.mg_state"
                    @change="handleState(scope.row.id, scope.row.mg_state)"
                  >
                  </el-switch>
                </template>
              </el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <div class="user-operate">
                    <UserEditDialog class="user-edit" :scopeRow="scope.row" />
                    <el-button
                      @click="delUser(scope.row.id)"
                      size="mini"
                      type="danger"
                      icon="el-icon-delete"
                    ></el-button>
                    <AssignRoleDialog
                      @updateUserList="updateUserList"
                      class="role-assign"
                      :scopeRow="scope.row"
                    />
                  </div>
                </template>
              </el-table-column>
            </el-table>
            <div class="block">
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage"
                :page-sizes="[1, 2, 5, 10]"
                :page-size="pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="5"
              >
              </el-pagination>
            </div>
          </div>
        </div>
      </template>
    </MainView>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import MainView from "../components/MainView.vue";
import AddUserDialog from "../components/userManage/AddUserDialog.vue";
import UserEditDialog from "../components/userManage/UserEditDialog.vue";
import AssignRoleDialog from "../components/userManage/AssignRoleDialog.vue";
export default {
  components: {
    MainView,
    UserEditDialog,
    AddUserDialog,
    AssignRoleDialog,
  },
  data() {
    return {
      // 
      currentPage: 1,
      // 2
      pageSize: 2,
      // 
      searchValue: "",
    };
  },
  methods: {
    ...mapActions([
      "delUserItem",
      "sizeChange",
      "currentPageChange",
      "changeState",
      "searchUser",
      "getUserList",
    ]),
    // 
    delUser(id) {
      this.delUserItem(id);
    },
    // 
    handleSizeChange(val) {
      this.pageSize = val;
      this.sizeChange({ pagenum: this.currentPage, pagesize: val });
    },
    // 
    handleCurrentChange(val) {
      this.currentPageChange({ pagenum: val, pagesize: this.pageSize });
    },
    // 
    handleState(id, type) {
      this.changeState({ uid: id, type: !type });
    },
    // 
    search() {
      const tableData = this.$store.state.users.tableData;
      const { searchValue, pageSize, currentPage } = this;
      if (searchValue) {
        const user = tableData.find(
          (ele) =>
            ele.username.includes(searchValue) ||
            ele.mobile.includes(searchValue) ||
            ele.email.includes(searchValue)
        );
        this.searchUser(user.id);
      } else {
        // 
        this.getUserList({ pagenum: currentPage, pagesize: pageSize });
      }
    },
    // 
    updateUserList() {
      const { pageSize, currentPage } = this;
      console.log(pageSize, currentPage);
      this.getUserList({ pagenum: currentPage, pagesize: pageSize });
    },
  },
  computed: {
    showUserData() {
      return this.$store.state.users.tableData;
    },
  },
  created() {
    const { pageSize, currentPage } = this;
    this.getUserList({ pagenum: currentPage, pagesize: pageSize });
  },
};
</script>

<style lang="less" scoped>
.user-list {
  .user-main {
    .user-operate-top {
      display: flex;
    }
    .search {
      max-width: 350px;
      margin-right: 30px;
    }
    .add {
      vertical-align: top;
      margin-left: 30px;
    }
    .user-show {
      margin-top: 20px;
      .block {
        margin-top: 20px;
      }
      .user-operate {
        display: flex;
        .user-edit {
          margin-right: 10px;
        }
        .role-assign {
          margin-left: 10px;
        }
      }
    }
  }
}
</style>